<template>
  <div>
    <img
      :src="switchVoiceForStatus ? voice : Keyboard"
      @click="switchVoice"
      class="chatSearchVoice"
    />
  </div>
  <!--<audio :src="audioSrc" controls></audio>-->
</template>

<script>
import { ref, watch } from "vue";
import { useStore } from "vuex";
import { GetRecordingText } from "@/api/RecordingAxiosApi";
import voice from "@/assets/images/.png";
import Keyboard from "@/assets/images/.png";
// element
import { ElMessage } from "element-plus";
export default {
  name: "GetRecording",
  props: ["RecordingStatus", "MaskLayerShow"],
  setup(props, { emit }) {
    let store = useStore();
    const audioSrc = ref("");
    const chunks = ref([]); // 
    const getAudioTracks = ref(null); //   
    const recorder = ref(""); // MediaRecorder
    const switchVoiceForbidden = ref(true); // 
    const switchVoiceForStatus = ref(false); // 
    // 
    const startRecording = () => {
      switchVoiceForbidden.value = false; // 
      // 
      navigator.mediaDevices
        .getUserMedia({ audio: true })
        .then(function (stream) {
          getAudioTracks.value = stream.getAudioTracks()[0]; //  
          // MediaRecorder
          recorder.value = new MediaRecorder(stream);
          // 
          recorder.value.start();
          recorder.value.ondataavailable = function (event) {
            // Blob
            chunks.value = new Blob([event.data], { type: "audio/wav" });
          };
          // 
          recorder.value.addEventListener("stop", () => {
            store.commit("PhonetisStyleStatus", true); // 
            // audio
            // let url = URL.createObjectURL(chunks.value);
            // audioSrc.value = url;
            // URL
            // URL.revokeObjectURL(url);
            //  base64 
            blobToBase64(chunks.value, (res) => {
              // TaskId
              GetRecordingText({
                EngineModelType: "16k_zh",
                ChannelNum: 1,
                ResTextFormat: 0,
                SourceType: 1,
                Data: res,
              })
                .then((res) => {
                  const str = res.data;
                  // 
                  const result = str.replace(
                    /\[\d+:\d+\.\d+,\d+:\d+\.\d+\]\s*/g,
                    ""
                  );
                  //  
                  if (result.trim() === "") {
                    switchVoiceForbidden.value = true;
                    ElMessage("");
                  }
                  emit("RecordingText", result); // RecordingText // 
                  store.commit("PhonetisStyleStatus", false); // vuex   
                })
                .catch((err) => {
                  console.log(err);
                });
            });
          });
        });
    };
    // 
    const stopRecording = () => {
      // 
      recorder.value.stop();
      getAudioTracks.value.stop(); // 
    };
    //  
    const switchVoice = () => {
      // 
      if (switchVoiceForbidden.value) {
        switchVoiceForStatus.value = !switchVoiceForStatus.value;
        emit("switchVoice", switchVoiceForStatus.value); //  chat
      }
    };
    // blob base64 
    function blobToBase64(blob, callback) {
      var reader = new FileReader();
      reader.onload = function () {
        var dataUrl = reader.result;
        var base64 = dataUrl.split(",")[1];
        callback(base64);
      };
      reader.readAsDataURL(blob);
    }
    // PhoneticStyle 
    // bug  props.RecordingStatus
    watch(
      () => props.RecordingStatus,
      (newVal) => {
        if (newVal) {
          startRecording();
        } else {
          stopRecording();
        }
      }
    );
    // chat   
    watch(
      () => props.MaskLayerShow,
      (newVal) => {
        if (!newVal) {
          switchVoiceForbidden.value = true; //   
        } else {
          switchVoiceForbidden.value = false; //   
        }
      }
    );
    return {
      startRecording,
      stopRecording,
      audioSrc,
      voice,
      switchVoice,
      Keyboard,
      switchVoiceForbidden,
      switchVoiceForStatus,
    };
  },
};
</script>

<style lang="scss" scoped>
.chatSearchVoice {
  transform: translate(-100%, 50%);
  height: 0.53rem;
  position: absolute;
  cursor: pointer;
  bottom: 50%;
}
</style>
